.user-list{
  display:-ms-flexbox;
  display:flex;
  padding:0;
  margin:0;
  list-style-type: none;
}

.user-list__item{
  width:250px;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  margin-right:20px;
}

.user-list__img{
  height:100px;
  -o-object-fit: cover;
     object-fit: cover;
}
.user-repo{
  overflow: hidden;
  padding: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.user-repo li{
  -webkit-transform: translateX(-350px);
      -ms-transform: translateX(-350px);
          transform: translateX(-350px);
  -webkit-transition:0.6s;
  -o-transition:0.6s;
  transition:0.6s;
}
.user-repo.opened li{
  -webkit-transform: translateX(0px);
      -ms-transform: translateX(0px);
          transform: translateX(0px);
}
.user-repo__title{
  cursor:pointer;
}



